.ri-wrapper,
.ri-cursor-plus {
  cursor:
    url('data:image/png;base64,')
      25 25,
    no-drop;
}

.ri-container,
.ri-cursor-x {
  cursor:
    url('data:image/png;base64,')
      25 25,
    no-drop;
}

.ri-wrapper {
  display: inline-block;
  margin: 0 8px;
}

.ri-wrapper a.clicked {
  box-shadow: inset 0 0 0 500px rgba(0, 0, 0, 0.2);
}

.ri-container {
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  overflow: hidden;
  z-index: 999999;
  margin: 0px;
  transition: opacity 150ms cubic-bezier(0, 0, 0.26, 1);
}

.ri-caption-container {
  font-family: Georgia, Times, 'Times New Roman', serif;
  position: fixed;
  bottom: 0px;
  left: 0px;
  padding: 20px;
  color: #fff;
  word-spacing: 0.2px;
  text-shadow: -1px 0px 1px rgba(0, 0, 0, 0.4);
}

.ri-title {
  margin: 0px;
  padding: 0px;
  font-weight: normal;
  font-size: 40px;
  letter-spacing: 0.5px;
  line-height: 35px;
  text-align: left;
}

.ri-caption {
  margin: 0px;
  padding: 0px;
  font-weight: normal;
  font-size: 20px;
  letter-spacing: 0.1px;
  max-width: 500px;
  text-align: left;
  background: none;
  margin-top: 5px;
}

.ri-loader {
  position: relative;
  left: 30%;
  top: 30%;
}

.ri-loader > div {
  width: 12px;
  height: 12px;
  margin-left: 2px;
  margin-top: 2px;
}

.ri-loader > div > div {
  background: #fff;
}

.ri-trigger {
  display: inline-block;
  width: 200px;
  height: 200px;
  background-size: cover;
  background-position: 50% 50%;
}
